import Vue from 'vue'

const files = require.context('.', false, /\.js$/)
files.keys().forEach(key => {
  //index.js.ex.js()
  if (!key || key === './index.js' || key.toString().endsWith('.ex.js')) return
  const plugin = files(key).default
  if (!plugin) {
    throw new Error('' + key + '')
  }
  Vue.use(plugin)
})
